@media (max-width: 993px) {
  .form {
    margin-top: 50px;
    &__element {
      margin: 0px auto 50px;
    }
    &__wrapper {
      margin: 0;
    }

    &__button {
      padding: 0;
    }
  }

  .sign {
    &__with-buttons {
      margin: 0;
      flex-wrap: wrap;
      button {
        margin-top: 50px;
      }
    }
    &__login {
      margin-bottom: 50px;
    }
  }
}

@media (max-width: 770px) {
  .sign {
    &__title {
      font-size: 45px;
    }
    &__subtitle {
      font-size: 20px;
    }

    &__or {
      font-size: 40px;
    }
  }
}

@media (max-width: 480px) {
  .sign {
    &__title {
      font-size: 35px;
    }

    &__or {
      font-size: 40px;
    }
  }
}
