.team {
  margin: 200px 0 200px;
  &__uptitle {
    font-size: 25px;
    font-weight: 600;
    color: $accent;
    text-align: center;
  }

  &__title {
    font-size: 60px;
    font-weight: 600;
    color: white;
    text-align: center;
  }

  &__image {
    margin-top: 60px;
  }

  &__name {
    font-size: 28px;
    font-weight: 700;
    text-align: left;
    color: white;
  }

  &__work {
    font-size: 19px;
    font-weight: 400;
    text-align: left;
    color: $grey;
  }

  &__link {
    margin-top: 15px;
    a {
      text-decoration: none;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      color: white;
    }
  }
}
