.source {
  &__cards {
    margin-top: 95px;
    justify-content: center;
  }

  &__title {
    font-size: 80px;
    font-weight: 600;
    text-align: center;
    color: white;
    margin-top: 100px;
  }

  &__descr {
    font-size: 30px;
    font-weight: 400;
    text-align: center;
    color: white;
  }

  &__card {
    position: relative;
    padding: 20px;
    width: 350px;
    margin-left: 20px;
    margin-top: 50px;
    background: transparent;
    border: 1px solid $light-grey;
    transition: all 0.5s;
    border-radius: 10px;
    &:hover {
      background: $light-grey;
      transition: all 0.5s;
    }
  }

  &__card-title {
    font-size: 24px;
    font-weight: 600;
    text-align: left;

    margin-top: 15px;
    color: white;
  }

  &__card-descr {
    margin-top: 15px;
    font-size: 20px;
    font-weight: 400;
    text-align: left;

    color: $grey;
  }

  &__card-soon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 130px;
    color: rgba(74, 125, 227, 1);
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    position: absolute;
    top: 20px;
    right: 20px;
    height: 35px;
    background: #102142;
    border-radius: 10px;
  }
}

.help {
  margin-top: 200px;
}
