.sign {
  &__title {
    margin-top: 100px;
    font-size: 60px;
    font-weight: 600;
    color: white;
    text-align: center;
  }

  &__subtitle {
    color: white;
    font-size: 22px;
    font-weight: 400;
    text-align: center;
  }

  &__or {
    font-size: 45px;
    font-weight: 600;
    text-align: center;
    margin-top: 75px;
    color: white;
  }

  &__with {
    font-size: 22px;
    font-weight: 400;
    text-align: center;
    color: white;
  }

  &__with-buttons {
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
  }

  &__login {
    margin-top: 30px;
    color: white;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    a {
      text-decoration: none;
      color: white;
      color: $accent;
    }
  }
}
