@media (max-width: 1200px) {
  .mission {
    &__title {
      width: 90%;
      font-size: 50px;
    }
    &__text {
      text-align: justify;
      margin: 100px auto 0;
    }

    &__author {
      font-size: 24px;
    }
  }
}

@media (max-width: 993px) {
  .mission {
    &__title {
      font-size: 40px;
    }
    &__descr {
      font-size: 25px;
      width: 90%;
      margin-top: 30px;
    }
  }
}

@media (max-width: 770px) {
  .mission {
    &__logo {
      font-size: 35px;
    }
    &__uptitle {
      font-size: 20px;
    }
    &__title {
      font-size: 30px;
      width: 100%;
    }
    &__descr {
      font-size: 22px;
      width: 100%;
      margin-top: 30px;
    }
    &__text {
      font-size: 20px;
    }

    hr {
      width: 100%;
    }
  }
  .team {
    &__uptitle {
      font-size: 20px;
    }

    &__title {
      font-size: 40px;
    }
  }
}

@media (max-width: 480px) {
  .mission {
    &__logo {
      font-size: 30px;
    }
    &__uptitle {
      font-size: 18px;
    }
    &__title {
      font-size: 22px;
    }
    &__descr {
      font-size: 16px;
    }
    &__text {
      font-size: 14px;
    }

    &__author {
      font-size: 18px;
    }

    hr {
      display: none;
    }
  }
  .team {
    &__uptitle {
      font-size: 18px;
    }

    &__title {
      font-size: 32px;
    }

    &__name {
      font-size: 20px;
    }

    &__work {
      font-size: 16px;
    }
  }
}
