.mission {
  margin-top: 100px;
  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 55px;
    font-weight: 800;
    text-align: left;
    img {
      width: 75px;
      margin-right: 15px;
    }
  }

  &__uptitle {
    font-size: 25px;
    font-weight: 600;
    text-align: center;
    color: $accent;
    margin-top: 60px;
  }

  &__title {
    font-size: 60px;
    width: 75%;
    font-weight: 600;
    text-align: center;
    color: white;
    margin: 0 auto;
  }

  &__descr {
    font-size: 24px;
    font-weight: 400;
    width: 75%;
    margin: 0 auto;
    text-align: center;
    color: $grey;
  }

  &__text {
    margin-top: 150px;
    font-size: 24px;
    font-weight: 400;
    text-align: left;
    color: rgba(255, 255, 255, 0.95);
    word-spacing: 5px;
  }

  hr {
    border: none;
    height: 2px;
    width: 120%;
    background: $grey;
    margin: 50px auto 0;
    margin-left: -10%;
  }

  &__author {
    margin-top: 50px;
    font-size: 28px;
    font-weight: 600;
    color: white;
    text-align: left;
  }
}
