@media (max-width: 1200px) {
  .source {
    &__title {
      font-size: 55px;
    }
    &__descr {
      font-size: 26px;
    }
  }
}

@media (max-width: 993px) {
  .source {
    &__title {
      font-size: 45px;
    }
    &__descr {
      font-size: 22px;
    }

    &__card {
      width: 300px;
    }

    &__card-title {
      font-size: 20px;
    }

    &__card-descr {
      font-size: 18px;
    }
  }
}

@media (max-width: 770px) {
  .source {
    &__title {
      font-size: 35px;
    }
    &__descr {
      font-size: 18px;
    }

    &__card {
      width: 400px;
      margin-left: 0;
      margin-top: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    &__card-title {
      font-size: 20px;
    }

    &__card-descr {
      font-size: 18px;
      text-align: center;
    }
  }
}

@media (max-width: 480px) {
  .source {
    &__title {
      font-size: 25px;
    }
    &__descr {
      font-size: 16px;
    }

    &__card {
      width: 280px;
      margin-top: 20px;
    }

    &__card-title {
      font-size: 16px;
    }

    &__card-descr {
      font-size: 14px;
    }

    &__card-soon {
      width: 75px;
      font-size: 12px;
    }
  }
}
